@media (max-width: 500px) {
    body.responsive-check::before {
      content: 'mobile';
      display: none;
    }
  }
  

/* To hide the scroll bar */
::-webkit-scrollbar {
    width: 0;
}

::-moz-scrollbar {
    width: 0;
}

::-o-scrollbar {
    width: 0;
}

::-ms-scrollbar {
    width: 0;
}

scrollbar {
    width: 0;
}
